// @ is an alias to /src
import { mapState } from 'vuex'

export default {
  name: 'NavDrawer',
  components: {
  },
  data: () => ({
    drawer: true
  }),
  computed:{
    Menus: {
      get(){
        return this.$store.state.MenuItems
      }
    }
  },
  methods:{
    ToggleDrawer(drawer){
      this.drawer = drawer
    }
  },
  watch:{
    drawer: function(newVal, oldVal) {
      this.$emit('drawer-changed', this.drawer)
    }
  }
}
